import React from 'react';
import { Button, Panel, PanelHeader, Group, Div } from '@vkontakte/vkui';
import { Icon28AddOutline } from "@vkontakte/icons";
import { PanelProps } from '@vkontakte/vkui/dist/components/Panel/Panel';
import { Contacts, Dashboard } from '../../components';

/**
 * The app panel.
 *
 * @param props
 * @constructor
 */
export default function AppPanel(props: PanelProps & { onToggleEventModal: () => void; }): React.ReactElement {
  return (
    <Panel id={props.id}>
      <PanelHeader>Долги</PanelHeader>
      <Group>
        <Div>
          <Button before={<Icon28AddOutline />} size="l" stretched onClick={() => props.onToggleEventModal()}>
            Добавить долг
          </Button>
        </Div>
      </Group>
      <Dashboard />
      <Contacts />
    </Panel>
  );
}