import React from 'react';
import { bindActionCreators, Dispatch } from 'redux';
import { connect } from 'react-redux';
import { fetchAccessToken } from './store/actions/user';
import { CatalogPanel, AppPanel } from './panels';
import { IRouterProps, IStory, TabRoute } from './types';
import {
  AppRoot,
  Epic,
  FormItem,
  Group,
  Input,
  ModalPage,
  ModalPageHeader,
  ModalRoot,
  PanelHeaderButton,
  Tabbar,
  TabbarItem,
  View
} from '@vkontakte/vkui';

import {
  Icon24Cancel,
  Icon24Done,
  Icon28BookOutline,
  Icon28ServicesOutline
} from '@vkontakte/icons';

/**
 * The app router.
 *
 * @constructor
 */
function Router(props: IRouterProps): React.ReactElement {
  const [activeStory, setActiveStory] = React.useState<IStory>(TabRoute.App);
  const [activeModal, setActiveModal] = React.useState<IStory | null>(null);

  React.useEffect(() => {
    props.fetchAccessToken();
  }, [props]);

  function onStoryChange(e: any): void {
    return setActiveStory(e.currentTarget.dataset.story);
  }

  const addEventModal = (
    <ModalRoot activeModal={activeModal} onClose={() => setActiveModal(null)}>
      <ModalPage id="add-event" header={
        <ModalPageHeader
          left={<PanelHeaderButton onClick={() => setActiveModal(null)}><Icon24Cancel /></PanelHeaderButton>}
          right={<PanelHeaderButton><Icon24Done /></PanelHeaderButton>}
        >Добавить долг</ModalPageHeader>
      }>
        <Group>
          <FormItem top="Сумма">
            <Input placeholder="К примеру: 347 ₽" />
          </FormItem>
          <FormItem top="Контакт">
            <Input placeholder="К примеру: 347 ₽" />
          </FormItem>
          <FormItem top="Дата возврата">
            <Input placeholder="К примеру: 347 ₽" />
          </FormItem>
        </Group>
      </ModalPage>
    </ModalRoot>
  );

  return (
    <AppRoot>
      <Epic activeStory={activeStory} tabbar={
        <Tabbar>
          <TabbarItem
            onClick={onStoryChange}
            selected={activeStory === TabRoute.Catalog}
            data-story={TabRoute.Catalog}
          ><Icon28BookOutline /></TabbarItem>
          <TabbarItem
            onClick={onStoryChange}
            selected={activeStory === TabRoute.App}
            data-story={TabRoute.App}
          ><Icon28ServicesOutline /></TabbarItem>
        </Tabbar>
      }>
        <View id={TabRoute.Catalog} activePanel={TabRoute.Catalog}>
          <CatalogPanel id={TabRoute.Catalog} />
        </View>
        <View id={TabRoute.App} modal={addEventModal} activePanel={TabRoute.App}>
          <AppPanel id={TabRoute.App} onToggleEventModal={() => setActiveModal('add-event')} />
        </View>
      </Epic>
    </AppRoot>
  );
}

const mapDispatchToProps = (dispatch: Dispatch) => ({
  fetchAccessToken: bindActionCreators(fetchAccessToken, dispatch)
});

export default connect(null, mapDispatchToProps)(Router);